export default {
	name:"list",
	props: {
		list: null
	},
	computed: {
		getImg() {
			if(this.list.type === "usdt_money") {
				return '../../../../../static/img/index/USDT.png'
			}else if(this.list.type === "xcoin_use") {
				return '../../../../../static/img/index/xev.png'
			}else if(this.list.type === "jifen_money") {
				return '../../../../../static/img/index/xev.png'
			}else if(this.list.type === "flat_money") {
				return '../../../../../static/img/index/xv.png'
			}
		},
		getText() {
			if(this.list.type === "usdt_money") {
				return 'USDT'
			}else if(this.list.type === "xcoin_use") {
				return 'XEV'
			}else if(this.list.type === "jifen_money") {
				return 'XEV'
			}else if(this.list.type === "flat_money") {
				return 'XV'
			}
		}
	}
}
